*{
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif,'\5FAE\8EDF\6B63\9ED1\9AD4';
  box-sizing: border-box;
}

.filter_ul{
    max-height: 30rem;
    overflow: auto;
    border: 1px solid #aaa;
}
.Game_board__2YLv1{
    position: relative;
    display: block;
    width: 21rem;
    height: 21rem;
    font-size: 0;
}
.Game_board__2YLv1 > div:nth-child(1){
    border-top: 1px solid #ccc;
    border-left: 1px solid #ccc;
    border-bottom: 0.5px solid #ccc;
    background-color: #ccc;
}
.Game_grid__wxxqC{
    position: relative;
    display: inline-block;
    width: calc(100%/3);
    height: 0;
    padding-bottom: calc(100%/3);
    border: 1px solid #ccc;
    font-size: 4rem;
    background-color: #fff;
}
.Game_grid__wxxqC:hover{
    cursor: pointer;
    border: 1px solid #f00;
}

.Game_grid__wxxqC span{
    position: absolute;
    top:50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.Game_player__9Tkwh,.Game_winner__2p6f2{
    font-size: 1rem;
}

button{
    font-size: 1rem;
}

.login_navber{
    background-color: rgb(58, 58, 58);
}
.todolist_li:hover{
    cursor: pointer;
    color: brown;
}
